import React from "react"
import Img from "gatsby-image"
import { Link } from "gatsby"
import { Search, Clear } from "./icons"
import { useStaticQuery, graphql } from "gatsby"

import "./navbar.scss"

const NavBar = ({title, toggleDarkMode}) => {
  const data = useStaticQuery(graphql`
    query LogoQuery {
      file: file(absolutePath: { regex: "/logo.png/" }) {
        childImageSharp {
          fixed(width: 250, height: 250, quality: 100) {
            ...GatsbyImageSharpFixed
          }
        }
      }
    }
  `)

  const placeHolderText = `Generate Tweets for @user`

  return (
    <>
      <div className={`navbar`}>
        <div className={`navbar-inner`}>

          <Link to={`/`}>
            <Img fixed={data.file.childImageSharp.fixed} className={`navbar-logo`} />
          </Link>
          
          <div className={`search-bar`}>
            <span className={`search-icon-wrapper`}>{Search}</span>
            <span className={`search-box`}><input placeholder={placeHolderText} /></span>
            <span className={`clear-icon-wrapper`}>{Clear}</span>
          </div>

          <button className={`dark-mode-toggle`} onClick={toggleDarkMode}>🌛</button>

        </div>
      </div>
    </>
  )
}

export default NavBar
